import React, { useEffect, useState } from 'react';
import { colors, Container, Typography, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';

import { Layout } from '@/components/Layout';

export default function Example() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  return (
    <>
      {isMounted && (
        <Layout header={<></>}>
          <Container
            disableGutters
            maxWidth="md"
            sx={{ mx: 'auto', px: isMobile ? 6 : 0, py: 4 }}
          >
            <Typography variant="h1">
              React + StyleX + Vite
              <br />
              티스토리 스킨
            </Typography>
            <Typography
              color={colors.grey[500]}
              fontSize={24}
              fontWeight={700}
            >
              티스토리 스킨을 React 프레임워크로 만들었습니다. <u>StyleX</u>로 스타일을 구성하고 Vite로 빌드합니다.
            </Typography>
            <Typography variant="h2">React 프레임워크의 한계</Typography>
            <Typography
              color={colors.grey[700]}
              fontSize={18}
            >
              티스토리 스킨은 치환자를 사용하여 제작되고 사용된 치환자에는 티스토리의 데이터가 대입되어 티스토리의
              디자인이 완성됩니다.
            </Typography>
          </Container>
        </Layout>
      )}
    </>
  );
}
